// The variables below are processed in './layouts/partials/css.html'
// when it's executed as template by Hugo pipes.

$content-font: 'Open Sans',sans-serif;
$content-rtl-font: Vazirmatn, sans-serif;

$base-color: #191A19;
$page-background-color: $base-color;
$special-color: #2D3642;
$highlight-color: #ffffff;
$text-color: #7a7a7a;

$nav-background-color: $special-color;
$nav-text-color: $page-background-color;
$algolia-search-box-color: #444;
$algolia-search-box-icon-color: #888;
$algolia-search-box-background-color: #fafafa;
$algolia-border-color: #e4e4e4;

$header-text-color: #FF8D00;

$logo-color: darken($page-background-color, 1);
$bubble-color: $highlight-color;
$bubble-background-color: #ccc;
$bubble-hover-color: $highlight-color;

$article-background-color: #343434;
$meta-text-color: #999999;
$meta-border-color: #eeeeee;
$continue-reading-hover-color: $meta-text-color;

$footer-background-color: $page-background-color;
$footer-headline-color: $highlight-color;

@mixin featuredImage() {
  .featured-image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 5;
    opacity: 0.25;
  }
}

// Dependencies
@import '_vendor/normalize';
@import '_vendor/skeleton';
@import '_vendor/flexslider';
@import '_vendor/dimbox';
@import '_vendor/tooltipster';
@import '_vendor/tooltipster-sideTip-light';
@import 'fontface';

// Base theme styling
@import 'mostafa-hugo-theme';
