// Font Awesome
@import './webfonts/fontawesome-free-6.7.2-web/css/brands.css';
@import './webfonts/fontawesome-free-6.7.2-web/css/regular.css';
@import './webfonts/fontawesome-free-6.7.2-web/css/solid.css';
@import './webfonts/fontawesome-free-6.7.2-web/css/fontawesome.css';

// Font Vazir Matn for Persian content
